import React, { useEffect, useState, useContext } from "react";
import axios from "axios";
import { useParams, useNavigate } from "react-router-dom";
import { Link } from "react-router-dom";
import CommentWrite from "../comment/CommentWrite";
import CommentList from "../comment/CommentList";
import { AuthContext } from "../context/AuthProvider";
import { HttpHeadersContext } from "../context/HttpHeadersProvider";

import "../../css/bbsdetail.css"; // :   import
import FileDisplay from "../file/FileDisplay";

function BbsDetail() {
  const { headers, setHeaders } = useContext(HttpHeadersContext);
  const { auth, setAuth } = useContext(AuthContext);
  const [ bbs, setBbs] = useState({});
  const { boardId } = useParams(); //  
  const navigate = useNavigate();
  const API_URL = process.env.REACT_APP_API_URL;

  const getBbsDetail = async () => {
    try {
      const response = await axios.get(`${API_URL}/board/${boardId}`);

      console.log("[BbsDetail.js] getBbsDetail() success :D");
      console.log(response.data);

      setBbs(response.data);
    } catch (error) {
      console.log("[BbsDetail.js] getBbsDetail() error :<");
      console.error(error);
    }
  };

  const deleteBbs = async () => {
    try {
      const response = await axios.delete(`${API_URL}/board/${boardId}/delete`, {headers: headers});

      console.log("[BbsDetail.js] deleteBbs() success :D");
      console.log(response.data);

      if (response.status == 200) {
        alert("   :D");
        navigate("/bbslist");
      }
    } catch (error) {
      console.log("[BbsDetail.js] deleteBbs() error :<");
      console.error(error);
    }
  };

  useEffect(() => {
	//    localStorage   headers 
	setHeaders({
		"Authorization": `Bearer ${localStorage.getItem("bbs_access_token")}`
	});
    getBbsDetail();
  }, []);

  const updateBbs = {
    boardId: bbs.boardId,
    writerName: bbs.writerName,
    title: bbs.title,
    content: bbs.content,
	files: bbs.files
  };

  const parentBbs = {
    boardId: bbs.boardId,
    title: bbs.title,
  };

	return (
		<div className="bbs-detail-container">
			<div>

				<div className="my-3 d-flex justify-content-end">
					<Link className="btn btn-outline-secondary" to="/bbslist"><i className="fas fa-list">
						</i> </Link> &nbsp;

					<Link className="btn btn-outline-secondary" to={{pathname: `/bbsanswer/${bbs.boardId}` }} state={{ parentBbs: parentBbs }}>
						<i className="fas fa-pen"></i> </Link> &nbsp;

				{
					/*     ,   */
					(localStorage.getItem("id") == bbs.writerName) ?
						<>
							<Link className="btn btn-outline-secondary"  to="/bbsupdate" state={{ bbs: updateBbs }}><i className="fas fa-edit"></i> </Link> &nbsp;
							<button className="btn btn-outline-danger"  onClick={deleteBbs}><i className="fas fa-trash-alt"></i> </button>
						</>
					:
					null
				}

				</div>

				<table className="table table-striped">
				<tbody>
				<tr>
					<th className="col-3"></th>
					<td>
					<span>{bbs.writerName}</span>
					</td>
				</tr>

				<tr>
					<th></th>
					<td>
					<span>{bbs.title}</span>
					</td>
				</tr>

				<tr>
					<th></th>
					<td>
					<span>{bbs.createdDate}</span>
					</td>
				</tr>

				<tr>
					<th></th>
					<td>
					<span>{bbs.viewCount}</span>
					</td>
				</tr>

				<tr>
					<th></th>
					<td></td>
				</tr>
				</tbody>
				</table>

				<div className="content-box">{bbs.content}</div>
				<div>
					<FileDisplay files={bbs.files} boardId={boardId} />
				</div>
				
				 {/*    */}
				 <CommentList boardId={boardId} />

				{/*    */}
				{
					(auth) ? //     
						<CommentWrite boardId={boardId}/>
					:
						null
				}


			</div>
		</div>
	);
}

export default BbsDetail;