import axios from "axios";
import React , { useRef } from "react";
import { useContext, useState } from "react";
import { useParams, useNavigate } from "react-router-dom"
import { AuthContext } from "../context/AuthProvider";
import { HttpHeadersContext } from "../context/HttpHeadersProvider";


/*   */
function Comment(props) {
	const {auth, setAuth} = useContext(AuthContext);
	const {headers, setHeaders} = useContext(HttpHeadersContext);
	
	const page = props.page;
	const comment = props.obj;
	const commentId = comment.commentId;
	const { boardId } = useParams(); // boardId,  

	const [show, setShow] = useState(false);

	const [content, setContent] = useState(comment.content);
	const changeContent = (event) => {
		setContent(event.target.value);
	};
	const API_URL = process.env.REACT_APP_API_URL;


	/*   */
	const updateComment = async () => {

		const req = {
			content: content
		};

		await axios.patch(`${API_URL}/board/${boardId}/comment/update/${commentId}`, req, {headers: headers})
		.then((resp) => {
			console.log("[Comment.js] updateComment() success :D");
			console.log(resp.data);

			alert("   !");

			//     
			props.getCommentList(page);

		}).catch((err) => {
			console.log("[Comment.js] updateComment() error :<");
			console.log(err);

			alert(err);

		});
		updateToggle();
	}

	/*   */
	const deleteComment = async () => {
		await axios.delete(`${API_URL}/board/${boardId}}/comment/delete/${commentId}`, {headers: headers})
			.then((resp) => {
				console.log("[BbsComment.js] deleteComment() success :D");
				console.log(resp.data);

				alert("   :D");
				//    
				props.getCommentList(page);

			}).catch((err) => {
				console.log("[BbsComment.js] deleteComment() error :<");
				console.log(err);
			});
	}

	function updateToggle() { 
		setShow(show => !show) 
	}

	

	//    
	//if (comment.del == 0) {
		return (
			<>
				{/*   ( ,  ,  ) */}
				<div className="my-1 d-flex justify-content-center">
					<div className="col-1">
						<img src="/images/profile-placeholder.png" alt=" "
							className="profile-img" />
					</div>
					<div className="col-5">
						<div className="row">
							<span className="comment-id">{comment.commentWriterName}</span>
						</div>
						<div className="row">
							<span>{comment.createdDate}</span>
						</div>
					</div>

					<div className="col-4 d-flex justify-content-end">
					{
						/*        */
						(localStorage.getItem("id") == comment.commentWriterName) ?
							<>
								<button className="btn btn-outline-secondary" onClick={updateToggle}><i className="fas fa-edit"></i> </button> &nbsp; 
								<button className="btn btn-outline-danger" onClick={deleteComment}><i className="fas fa-trash-alt"></i> </button>
							
							</>
							:
							null
					}
					</div>
				</div>

				{
					/*    */
					show ?
						<>
							{/*   (  +    ) */}
							<div className="my-3 d-flex justify-content-center">
								<textarea className="col-10" rows="5" value={content} onChange={changeContent}></textarea>
							</div>
							<div className="my-1 d-flex justify-content-center">
								<button className="btn btn-dark" onClick={updateComment}><i className="fas fa-edit"></i> </button>
							</div>
						</>
					:
						<>
							{/*   ( ) */}
							<div className="my-3 d-flex justify-content-center">
								<div className="col-10 comment">{content}</div>
							</div>
						</>
				}


			</>
		);
	//}

	// //   
	// else {
	// 	return (
	// 		<>
	// 			<div className="my-5 d-flex justify-content-center">
	// 				<div className="comment">
	// 					<span className="del-span">    .</span>
	// 				</div>
	// 			</div>
	// 		</>
	// 	);
	// }
}

export default Comment;