import React, { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import Pagination from "react-js-pagination";
import axios from "axios";

import "../../css/bbslist.css";
import "../../css/page.css";

function AdminList() {
  const [adminList, setAdminList] = useState([]);

  //  Hook
  const [choiceVal, setChoiceVal] = useState("");
  const [searchVal, setSearchVal] = useState("");

  // Paging
  const [page, setPage] = useState(1);
  const [pageSize, setPageSize] = useState(10);
  const [totalPages, setTotalPages] = useState(0);
  const [totalCnt, setTotalCnt] = useState(0);

  const API_URL = process.env.REACT_APP_API_URL;

  //   
  const getAdminList = async (page) => {
    try {
		const response = await axios.get(`${API_URL}/user/memberlist`, {
			params: {"page": page - 1},
		  });

      console.log("[AdminList.js] useEffect() success :D");
      console.log(response.data);

      setAdminList(response.data.content);
      setPageSize(response.data.pageSize);
      setTotalPages(response.data.totalPages);
      setTotalCnt(response.data.totalElements);
    } catch (error) {
      console.log("[AdminList.js] useEffect() error :<");
      console.log(error);
    }
  };

  //  
  const search = async () => {
    try {
      const response = await axios.get(`${API_URL}/users/search`, {
        params: {
          page: page - 1,
          title: choiceVal === "title" ? searchVal : "",
          content: choiceVal === "content" ? searchVal : "",
          writerName: choiceVal === "writer" ? searchVal : "",
        },
      });

      console.log("[AdminList.js searchBtn()] success :D");
      console.log(response.data);

      setAdminList(response.data.content);
      setTotalCnt(response.data.totalElements);
    } catch (error) {
      console.log("[AdminList.js searchBtn()] error :<");
      console.log(error);
    }
  };

  //   ,   
  useEffect(() => {
    getAdminList(1);
  }, []);

  //   
  const changeChoice = (event) => { setChoiceVal(event.target.value);};
  const changeSearch = (event) => { setSearchVal(event.target.value);};

  //   
  const changePage = (page) => {
    setPage(page);
    getAdminList(page);
  };

  return (
    <div>
      {/*  */}
      <table className="search">
        <tbody>
          <tr>
            <td>
              <select
                className="custom-select"
                value={choiceVal}
                onChange={changeChoice}
              >
                <option>  </option>
                <option value="member_login_id"></option>
                <option value="member_name"> </option>
                <option value="member_role"> </option>
              </select>
            </td>
            <td>
              <input
                type="text"
                className="form-control"
                placeholder=""
                value={searchVal}
                onChange={changeSearch}
              />
            </td>
            <td>
              <button
                type="button"
                className="btn btn-outline-secondary"
                onClick={search}
              >
                <i className="fas fa-search"></i>
              </button>
            </td>
          </tr>
        </tbody>
      </table>
      <br />

      <table className="table table-hover">
        <thead>
          <tr>
            <th className="col-1"></th>
            <th className="col-3"></th>
            <th className="col-2"> </th>
            <th className="col-2"> </th>
            <th className="col-2"></th>
            <th className="col-4"></th>
          </tr>
        </thead>

        <tbody>
          {adminList.map(function (members, idx) {
            return <TableRow obj={members} key={idx} cnt={idx + 1} />;
          })}
        </tbody>
      </table>

      <Pagination
        className="pagination"
        activePage={page}
        itemsCountPerPage={pageSize}
        totalItemsCount={totalCnt}
        pageRangeDisplayed={totalPages}
        prevPageText={""}
        nextPageText={""}
        onChange={changePage}
      />

    </div>
  );
}

/*      */
function TableRow(props) {
  const member = props.obj;

  return (
    <tr>
      <th>{props.cnt}</th>
      <td>
        <Link to={{ pathname: `/memberdetail/${member.memberId}` }}>
          <span className="underline bbs-title">{member.email}</span>
        </Link>
      </td>
      <td>{member.username}</td>
      <td>{member.roles}</td>
      <td >{member.regionId}</td>
      <td>
        <button className="btn btn-outline-danger" onClick={() => {alert(' !')}}>
          <i className="fas fa-check"></i>  
        </button>
      </td>
    </tr>
  );
}

