import React, { useState, useEffect } from "react";
import { TODO_CATEGORY_ICON } from "../../constants/icon";
import KakaoMap from "../map/KakaoMap";
import AudioRecorder from "../ui/VoiceRecorder";

const TodoForm = ({ latParam, lngParam, files, setFiles:setFilesFromParam, onAdd, onClose }) => {
  const defaultCoord = {
    //   
    lat: latParam ? latParam : 37.581512341234,
    lng: lngParam ? lngParam : 126.886012341234,
  };

  //       
  const [title, setTitle] = useState("");
  const [summary, setSummary] = useState("");
  const [category, setCategory] = useState("Good"); //  
  const [coord, setCoord] = useState(defaultCoord); //     ->    
  // const [files, setFiles] = useState([]);

  const handleChangeFile = (event) => {
    //  5 
    const selectedFiles = Array.from(event.target.files).slice(0, 2);
    console.log ("TodoForm selected files : ", selectedFiles)
    // setFiles((prevFiles) => [...prevFiles, ...selectedFiles]);
    setFilesFromParam(selectedFiles);
  };

  const handleRemoveFile = (index) => {
    setFilesFromParam((prevFiles) => prevFiles.filter((_, i) => i !== index));
    
    // setFilesFromParam(files);
  };

  


  const getCoordFunction = () => {
    return coord;
  };
  const setCoordFunction = (coord) => {
    setCoord(coord);
  };

  // Todo     
  const addTodoHandler = () => {
    //    app.jsx
    //          

    // App.jsx  onAdd 
    // alert(coord)
    window.confirm(
      // " TodoForm setCoordFunction" +
      //   coord.lat +
      //   "," +
      //   coord.lng +
      //   "."
      " "
    )
      ? onAdd({ title, summary, category, coord, files })
      : console.log("  ");
    // onAdd({ title, summary, category, coord });
    onClose(); // add    
  };
  // Todo     
  const cancelHandler = () => {
    //   
    onClose(); // Modal   
  };

  return (
    <div>
      <form className="my-0">
        <div>
          <label
            className="block mb-2 text-xl text-gray-800"
            htmlFor="title"
          >
             
          </label>
          <input
            className="w-full p-2 border-[1px] border-gray-300 bg-gray-100 text-gray-900 rounded"
            type="text"
            id="title"
            value={title}
            onChange={(event) => setTitle(event.target.value)}
          />
        </div>
        <div>
          <label
            className="block mt-2 mb-2 text-xl text-gray-800"
            htmlFor="summary"
          >
             
          </label>
          <textarea
            className="w-full p-2 border-[1px] border-gray-300 bg-gray-100 text-gray-900 rounded"
            id="summary"
            rows="5"
            value={summary}
            onChange={(event) => setSummary(event.target.value)}
          />
        </div>
        <div>
          <label
            className="block mt-2 mb-2 text-xl text-gray-800"
            htmlFor="summary"
          >
             
          </label>
          <AudioRecorder />
        </div>
        <div>
          <label
            className="block mt-2 mb-2 text-xl text-gray-800"
            htmlFor="category"
          >
            
          </label>
          <select
            className="w-full p-2 border-[1px] border-gray-300 bg-gray-100 text-gray-900 rounded"
            id="category"
            value={category}
            onChange={(event) => setCategory(event.target.value)}
          >
            <option value="Good">{TODO_CATEGORY_ICON.Good} Good</option>
            <option value="SoSo">{TODO_CATEGORY_ICON.SoSo} SoSo</option>
            <option value="Bad">{TODO_CATEGORY_ICON.Bad} Bad</option>
          </select>
        </div>
        <div>
          <label
            className="block mt-3 mb-2 text-xl text-gray-800"
            htmlFor="summary"
          >
            
          </label>
          <div className="w-full p-2 border-[1px] border-gray-300 bg-gray-100 text-gray-900 rounded">
            <KakaoMap
              getCoordFunction={getCoordFunction}
              setCoordFunction={setCoordFunction}
            />
          </div>
        </div>
        <div>
          <label
            className="block mt-3 mb-2 text-xl text-gray-800"
            htmlFor="summary"
          >
            
          </label>
          <div>
            {files.map((file, index) => (
                <div 
                  key={index} 
                  style={{ display: "flex", alignItems: "center"}}
                >
                  <p>
                    <strong>FileName:</strong> {file.name}
                  </p>
                  <button className="delete-button" type="button" onClick={() => handleRemoveFile(index)}>
                    x
                  </button>
                </div>
              ))}
              {files.length < 2 && (
                <div>
                  <input type="file" name="file" 
                    onChange={handleChangeFile} 
                    multiple="multiple" 
                  />
                </div>
              )}
          </div>
        </div>

        <div className="flex justify-end gap-4 mt-3">
          <button
            class="btn btn-outline-secondary"
            type="button"
            onClick={cancelHandler}
          >
            
          </button>
          <button
            class="btn btn-dark"
            type="button"
            onClick={addTodoHandler}
          >
             
          </button>
        </div>
      </form>
    </div>
  );
};

export default TodoForm;
